<template>


  <div style="display: flex;justify-content: space-between;align-content: space-between;">
  <div style="width: 30%;"></div>

    <el-input v-model="query" placeholder="Buscar veiculo" style="--el-input-border-radius: 5px;margin-right: 5px;"></el-input>
    <el-tooltip
        effect="dark"
        content="Cadastrar Grupo"
        placement="bottom"
    >
      <el-button type="primary" @click="editGroupRef.newGroup()" plain><i class="fas fa-folder-plus"></i></el-button>
    </el-tooltip>
    <el-tooltip
        effect="dark"
        content="Cadastrar Dispositivo"
        placement="bottom"
    >
      <el-button type="primary" @click="editDeviceRef.newDevice()"><i class="fas fa-plus"></i></el-button>
    </el-tooltip>

  </div>

  <div style="border: silver 1px solid; border-radius: 5px;margin-top: 20px;height: calc(100vh - 200px);">
    <div class="device">
      <div class="name" style="font-size: 12px;box-sizing: border-box;font-weight: 100;padding: 5px;flex: 1;">Titulo</div>
      <div class="name" style="font-size: 12px;box-sizing: border-box;font-weight: 100;padding: 5px;width: 45px">Status</div>
      <div class="name" style="font-size: 12px;box-sizing: border-box;font-weight: 100;padding: 5px;width: 100px;text-align: center;">Atualização</div>
      <div class="icons" style="font-size: 12px;font-weight: 100;margin-right: -2px;">&nbsp;</div>
    </div>
    <div ref="realDevices" @scroll="realScroll($event)" style="overflow-x: hidden;overflow-y: scroll;height: calc(100vh - 230px);">
      <div class="fakeScroll" :style="{height: (filteredDevices.length*33)+'px'}">
        <div v-for="(device) in chunkedDevices" :key="device.id" class="device" :class="{'isDisabled': device.disabled}" @click="markerClick(device.id)" @contextmenu.prevent="markerContext($event,device.id)" :set="position = store.getters['devices/getPosition'](device.id)">

          <div class="name" style="flex: 1;">{{device.name}}</div>
          <div class="name" style="width: 45px;text-align: center;font-size: 18px;box-sizing: border-box;overflow: hidden;">
            <el-tooltip :content="device.disabled ? 'Desabilitado' : device.status === 'online' ? 'Online': (device.status==='offline') ? 'Offline':'Desconhecido'">
              <i v-if="device.status==='online'" style="color: green;" class="fas fa-check-circle"></i>
              <i v-else-if="device.status==='offline'" style="color: red;" class="fas fa-exclamation-circle"></i>
              <i v-else class="fas fa-question-circle" style="color: orange;"></i>
            </el-tooltip>

          </div>
          <div class="name" style="width: 100px;box-sizing: border-box;overflow: hidden;white-space: nowrap;text-align: center;" >{{getLastUpdated(device.lastUpdate,now)}}</div>
          <div v-if="position" class="icons">


            <el-tooltip v-if="position.attributes.alarm" :content="position.attributes.alarm">
              <div :style="{color: 'red'}"><i class="fas fa-exclamation-triangle"></i></div>
            </el-tooltip>
            <el-tooltip v-else :content="'Nenhum Alerta'">
              <div :style="{color: 'silver'}"><i class="fas fa-exclamation-triangle"></i></div>
            </el-tooltip>

            <el-tooltip v-if="position.attributes.ignition===true" :content="'Ligada'">
              <div :style="{color: 'green'}"><i class="fas fa-key"></i></div>
            </el-tooltip>
            <el-tooltip v-else-if="position.attributes.ignition===false" :content="'Desligada'">
              <div :style="{color: 'red'}"><i class="fas fa-key"></i></div>
            </el-tooltip>
            <el-tooltip v-else :content="'Desconhecido'">
              <div :style="{color: 'silver'}"><i class="fas fa-key"></i></div>
            </el-tooltip>


            <el-tooltip v-if="position.attributes.blocked===true" :content=" 'Bloqueado'">
              <div :style="{color: 'red'}"><i class="fas fa-lock"></i></div>
            </el-tooltip>
            <el-tooltip v-else-if="position.attributes.blocked===false" :content=" 'Desbloqueado'">
              <div :style="{color: 'green'}"><i class="fas fa-lock-open"></i></div>
            </el-tooltip>
            <el-tooltip v-else :content=" 'Desconhecido'">
              <div :style="{color: 'silver'}"><i class="fas fa-lock-open"></i></div>
            </el-tooltip>



            <el-tooltip v-if="position.attributes.motion" :content="'Movendo'">
              <div :style="{color: 'black'}"><i class="fas fa-angle-double-right"></i></div>
            </el-tooltip>
            <el-tooltip v-else :content="'Parado'">
              <div :style="{color: 'silver'}"><i class="fas fa-angle-double-right"></i></div>
            </el-tooltip>

          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script setup>

import {ref,computed,inject,onMounted} from 'vue';
import {useStore} from "vuex"

const store = useStore();

const markerContext = inject('markerContext');
const markerClick = inject('markerClick');


const query = ref('');
const editDeviceRef = inject('edit-device');
const editGroupRef = inject('edit-group');

const now = ref(0);

setInterval(()=>{
  now.value = new Date();
},1000);



const realDevices = ref(null);
const offsetDevices = ref(0);
const maxDevices = ref(0);

onMounted(()=>{

  const real = realDevices.value;

  maxDevices.value = Math.floor(real.clientHeight / 33)+3;
  offsetDevices.value = Math.floor(real.scrollTop / 33);
});

const realScroll = (event)=>{
  const real = event.target;


  maxDevices.value = Math.floor(real.clientHeight / 33)+3;
  offsetDevices.value = Math.floor(real.scrollTop / 33);

}




const getLastUpdated = (t,tt)=>{

  const diff = Math.round((new Date(tt).getTime() - new Date(t).getTime())/1000);

  if(diff<0){
    return 'agora mesmo';
  }else if(diff>86400){
    const dias = Math.round(diff/86400);

    return dias+' dias';
  }else if(diff>3600){
    const horas = Math.round(diff/3600);

    return horas+' horas';
  }else if(diff>60){
    const minutos = Math.round(diff/60);

    return minutos+' minutos';
  }else{
    return diff+' segundos';
  }

}

const filteredDevices = computed(()=>{
  if(query.value === ''){
    return store.state.devices.deviceList;
  }else {
    const r = query.value.toLowerCase().matchAll(/(.*?):(?<sinal>\+|-|=)(?<tempo>\d*) (?<filtro>dias|minutos|horas|segundos)/gi)
    const s = r.next()


    return store.state.devices.deviceList.filter((d) => {

        d.icon.remove();

        if(s.value){

            if(s.value.groups.filtro==='dias'){
                const df = parseInt(s.value.groups.tempo)*86400;
                const diff = Math.round((new Date().getTime() - new Date(d.lastUpdate).getTime())/1000);


                console.log(df);
                console.log(diff);

              if(s.value.groups.sinal==='+' && diff>=df){
                d.icon.addToMap();
                return true;
              }else if(s.value.groups.sinal==='-' && diff<=df){
                d.icon.addToMap();
                return true;
              }else if(s.value.groups.sinal==='='){



                return false;
              }
            }
        }


        for(let k of Object.keys(d)){
          if(k==='status' && String(d[k]).toLowerCase().replace('unknown','desconhecido').match(query.value.toLowerCase())){

            d.icon.addToMap();
            return true;
          }else if(String(d[k]).toLowerCase().match(query.value.toLowerCase())){

            d.icon.addToMap();
            return true;
          }
        }



        for(let k of Object.keys(d.attributes)){
          if(d.attributes[k].toString().toLowerCase().match(query.value.toLowerCase())){

            d.icon.addToMap();
            return true;
          }
        }

      return false;
    });
  }

})


const chunkedDevices = computed(()=>{
  let tmp = Object.assign([],filteredDevices.value);




  return tmp.splice(0,maxDevices.value+offsetDevices.value);
});



</script>

<style scoped>
  .device{
    border-bottom: silver 1px solid;
    display: flex;
    flex-direction: row;
    text-align: center;
    cursor: pointer;
    margin-right: -1px;
  }
  .device:first-child{

  }

  .device:hover{
    background: rgba(0,0,0,0.03);
  }



  .device .name{
    font-size: 12px;
    padding: 7px;
    text-align: left;
    line-height: 14px;
    font-weight: 800;
    border-right: silver 1px dotted;
    width: 60%;
  }

  .icons{
    display: flex;
    justify-content: center;
    flex-direction: row-reverse;
    flex: 1;
    font-size: 11px;
  }

  .icons div{
    display: flex;
    justify-content: center;
    flex: 1;
    border-right: silver 1px dotted;
    padding: 7px;
    font-size: 11px;
  }
  .icons div i{
    font-size: 14px;
  }

  .icons div:first-child{
    border-right: none;
  }

  .icons div span{
    display: flex;
    padding: 2px;
    padding-left: 5px;
  }

  .subtitle{
    margin-top: 20px;
    font-weight: bold;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-black);
  }

  .subtitle i{
    font-size: 12px;
    margin-right: 3px;
  }

  .updated{
    font-size: 12px;
    margin-top: 5px;
    color: var(--text-silver);
  }

  .address{
    color: var(--text-blue);
    font-size: 15px;
    margin-top: 10px;
    margin-bottom: 20px;
    padding: 10px;
    line-height: 20px;
  }

  .isDisabled{
    background: #eeeeee;
    color: silver !important;
  }

</style>